.prize {
    overflow: hidden;
    margin-top: -50px;
    &__wrapper {
        padding: 140px 70px;
        background: rgba(116, 200, 8, 1);

        @media screen and (max-width: 1470px) {
            padding: 140px 40px;
        }

        @include vp-1023 {
            border-radius: 32px 32px 0 0;
            padding: 140px 20px;
        }

        @media screen and (max-width: 660px) {
            padding: 100px 20px;
        }

        @include vp-389 {
            padding: 100px 10px;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;

        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        border-radius: 40px;
        overflow: hidden;
        position: relative;
        background: rgba(242, 242, 242, 1);
        display: flex;
        flex-direction: column;
        min-height: 520px;
        padding: 30px;
        z-index: 0;

        @include vp-1279 {
            min-height: 450px;
        }

        @include vp-1023 {
            min-height: 400px;
        }

        @include vp-767 {
            padding: 15px 20px 20px;
            border-radius: 20px;
            min-height: 500px;
        }

        @media screen and (max-width: 660px) {
            min-height: 430px;
        }

        @media screen and (max-width: 520px) {
            min-height: 370px;
        }

        @include vp-389 {
            min-height: 300px;
        }
    }

    &__item:nth-child(3),
    &__item:nth-child(4) {
        min-height: 395px;

        @include vp-1279 {
            min-height: 350px;
        }

        @include vp-1023 {
            min-height: 300px;
        }

        @include vp-767 {
            min-height: 405px;
        }

        @media screen and (max-width: 660px) {
            min-height: 355px;
        }

        @media screen and (max-width: 520px) {
            min-height: 290px;
        }

        @include vp-389 {
            min-height: 230px;
        }
    }


    &__image {
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        width: 100%;
        height: auto;
    }
    


    &__item:nth-child(3) .prize__image {
        @media screen and (max-width: 520px) {
            left: 50px;
        }
    }


    &__item:nth-child(4) .prize__image {
        height: 100%;
    }

    &__image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__info {
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
    }

    &__name {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        color: rgba(60, 43, 139, 1);
        margin: 0;

        @include vp-1279 {
            font-size: 25px;
        }

        @include vp-1279 {
            width: 83%;
        }

        br {
            @include vp-1279 {
                display: none;
            }
        }

        @media screen and (max-width: 660px) {
            width: 60%;
        }

        @media screen and (max-width: 520px) {
            font-size: 22px;
        }
    }

    &__item:first-child .prize__name {
        @media screen and (max-width: 520px) {
            width: 180px;
        }
    }

    &__item:nth-child(2) .prize__name {
        @media screen and (max-width: 520px) {
            width: 150px;
        }
    }

    &__info span {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        gap: 10px;
        margin-top: auto;
        background: rgba(255, 255, 255, 1);
        padding: 10px 20px;
        border-radius: 30px;
        width: 150px;
        align-self: flex-end;
        justify-content: start;
        font-size: 18px;
        color: rgba(60, 43, 139, 1);
        @include vp-767 {
            font-size: 16px;
            padding: 8px 10px;
            width: 120px;
        }
    }

    &__item:nth-child(3) .prize__info span,
    &__item:nth-child(4) .prize__info span {
        background: rgba(60, 43, 139, 1);
        color: rgba(255, 255, 255, 1);
    }

    &__item:nth-child(3) .prize__info span svg circle,
    &__item:nth-child(4) .prize__info span svg circle {
        fill: rgba(242, 242, 242, 1);
    }
}