.result {
    overflow: hidden;
    margin-top: -50px;
    &__wrapper {
        background: rgba(116, 200, 8, 1);
        padding: 140px 70px;

        @media screen and (max-width: 1470px) {
            padding: 140px 40px;
        }

        @include vp-1023 {
            padding: 140px 20px;
        }

        @media screen and (max-width: 660px) {
            padding: 100px 20px 150px;
        }

        @include vp-389 {
            padding: 100px 10px 150px;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        align-items: center;

        @media screen and (max-width: 520px) {
            align-items: start;
        }
    }

    &__title {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 30px;

        @include vp-1279 {
            font-size: 50px;
        }

        @include vp-767 {
            font-size: 45px;
            margin-bottom: 20px;
        }

        @media screen and (max-width: 520px) {
            font-size: 35px;
            text-align: left;
            line-height: 100%;
        }

        @include vp-389 {
            font-size: 30px;
        }
    }

    &__title span {
        color: rgba(60, 43, 139, 1);
    }

    &__text {
        font-family: Inter;
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 24.8px;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 40px;

        @include vp-767 {
            font-size: 16px;
            margin-bottom: 30px;
        }

        @media screen and (max-width: 520px) {
            text-align: left;
        }

        @include vp-389 {
            font-size: 14px;
            max-width: 90%;
            line-height: 100%;
        }
    }

    &__links {
        display: flex;
        gap: 30px;
        margin-bottom: 60px;

        @include vp-767 {
            gap: 20px;
        }

        @media screen and (max-width: 520px) {
            flex-direction: column;
            gap: 10px;
            width: 100%;
            margin-bottom: 30px;
        }
    }

    &__link {
        background: rgba(255, 255, 255, 1);
        border-radius: 30px;
        padding: 12px 30px;
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0.5px;
        text-align: center;
        vertical-align: middle;
        color: rgba(81, 60, 176, 1);
        transition: .3s;

        @include vp-767 {
            font-size: 25px;
        }

        @media screen and (max-width: 660px) {
            font-size: 20px;
        }

        @include vp-389 {
            font-size: 16px;
        }
    }

    &__link:hover {
        opacity: 0.8;
    }

    &__subtitle {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(60, 43, 139, 1);
        margin: 0 0 40px;

        @include vp-1279 {
            font-size: 50px;
            margin-bottom: 30px;
        }

        @include vp-767 {
            font-size: 45px;
            margin-bottom: 20px;
        }

        @media screen and (max-width: 520px) {
            font-size: 35px;
            text-align: left;
            color: rgba(255, 255, 255, 1);
        }

        @include vp-389 {
            font-size: 30px;
        }
    }

    &__subtitle span {
        color: rgba(255, 255, 255, 1);

        @media screen and (max-width: 520px) {
            color: rgba(60, 43, 139, 1);
        }
    }

    &__btn {
        max-width: 100%;
        width: 100%;
        background: rgba(255, 255, 255, 1);
        padding: 14.5px;
        border-radius: 30px;
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 26px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0.5px;
        text-align: center;
        vertical-align: middle;
        color: rgba(81, 60, 176, 1);
        transition: .3s;

        @include vp-767 {
            font-size: 24px;
        }

        @media screen and (max-width: 520px) {
            font-size: 20px;
        }

        @include vp-389 {
            font-size: 16px;
        }
    }

    &__btn:hover {
        opacity: 0.8;
    }
}