.new-code {
    overflow: hidden;
    margin-top: -100px;
    &__wrapper {
        position: relative;
        padding: 90px 70px 200px;
        background: rgba(185, 227, 131, 1);
        border-radius: 60px;

        @media screen and (max-width: 1470px) {
            padding: 90px 40px 200px;
        }

        @include vp-1023 {
            border-radius: 32px;
            padding: 90px 20px 200px;
        }

        @media screen and (max-width: 660px) {
            padding: 60px 20px 150px;
        }

        @include vp-389 {
            padding: 50px 10px 150px;
        }
        
    }

    &__bg {
        position: absolute;
        width: 100%;
        height: 720px;
        left: 50%;
        transform: translate(-50%);
        bottom: -200px;

        @media screen and (max-width: 520px) {
            width: 90%;
            height: 450px;
            bottom: 0;
        }

        @include vp-389 {
            transform: translate(0);
            left: auto;
            right: 0;
            width: 90%;
            bottom: 30px;
            height: 280px;
        }
    }

    &__bg img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }

    &__info {
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__title {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 110%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(81, 60, 176, 1);
        margin: 0 0 30px;

        @include vp-1279 {
            font-size: 50px;
        }

        @include vp-767 {
            font-size: 45px;
        }

        @media screen and (max-width: 520px) {
            font-size: 35px;
            margin-bottom: 20px;
        }

        @include vp-389 {
            font-size: 30px;
            text-align: left;
        }
    }

    &__text {
        font-family: Inter;
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 24.8px;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: rgba(81, 60, 176, 1);
        margin: 0 0 30px;

        @include vp-767 {
            font-size: 16px;
            margin-bottom: 20px;
        }

        @include vp-389 {
            text-align: left;
            line-height: 120%;
        }
    }
    
    &__form {
        display: flex;
        flex-direction: column;
        gap: 20px;
        align-items: center;
        input {
            width: 330px;
            background: rgba(255, 255, 255, 1);
            border-radius: 30px;
            padding: 12px;
            display: flex;
            justify-content: center;
            text-align: center;
            color: #000;
            font-family: Coolvetica;
            font-weight: 400;
            font-style: Regular;
            font-size: 30px;
            leading-trim: NONE;
            line-height: 100%;
            letter-spacing: 7px;
            vertical-align: middle;

            @media screen and (max-width: 520px) {
                padding: 3px 12px;
            }

            @include vp-389 {
                width: 100%;
                font-size: 28px;
            }

        }

        input::placeholder {
            font-family: Coolvetica;
            font-weight: 400;
            font-style: Regular;
            font-size: 30px;
            leading-trim: NONE;
            line-height: 100%;
            letter-spacing: 0.5px;
            text-align: center;
            vertical-align: middle;
            color: rgba(81, 60, 176, 1);
        }

        @media screen and (max-width: 520px) {
            gap: 10px;
        }

        @include vp-389 {
            width: 100%;
        }
    }

    .custom-input {
        @include vp-389 {
            width: 100%;
        }
    }

    form {
        @include vp-389 {
            width: 100%;
        }
    }
  
    .code-btn {
        max-width: 250px;
        width: 100%;
        background: rgba(81, 60, 176, 1);
        border-radius: 30px;
        padding: 12px;
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0.5px;
        text-align: center;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        transition: .3s;

        @media screen and (max-width: 520px) {
            max-width: 330px;
            padding: 14px;
            font-size: 20px;
        }

        @include vp-389 {
            max-width: 100%;
            font-size: 16px;
        }
    }

    .code-btn:hover {
        opacity: 0.8;
    }
  
    
      
}