.intro-lk {
    position: relative;
    overflow: hidden;

    &__wrapper {
        background-color: rgba(116, 200, 8, 1);
        padding: 170px 70px 90px;
        position: relative;

        @media screen and (max-width: 1470px) {
            padding: 170px 40px 90px;
        }

        @include vp-1023 {
            padding: 120px 20px 60px;
        }
        
        @include vp-389 {
            padding: 120px 10px 60px;
        }
    }

    &__title {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 80px;
        leading-trim: NONE;
        line-height: 120%;
        letter-spacing: 0%;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 50px;

        span {
            color: rgba(60, 43, 139, 1);
        }

        @include vp-1279 {
            font-size: 60px;
            margin-bottom: 30px;
        }

        @include vp-767 {
            font-size: 50px;
            line-height: 100%;
        }

        @media screen and (max-width: 520px) {
            font-size: 40px;
            text-align: center;
        }
    }

    &__box {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        margin-bottom: 120px;

        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }

    &__card {
        border-radius: 40px;
        overflow: hidden;
        &--one {
            position: relative;
            background: rgba(255, 255, 255, 1);
            padding: 30px 30px 30px 40px;
            display: flex;
            flex-direction: column;

            @media screen and (max-width: 991px) {
                padding: 20px;
            }
        }

        &--two {
            background: rgba(81, 60, 176, 1);
            padding: 30px;
            display: flex;
            flex-direction: column;

            
            @media screen and (max-width: 991px) {
                padding: 20px;
            }
        }
    }

    &__card-bg {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 180px;
        height: 95px;

        @include vp-767 {
            display: none;
        }
    }

    &__card-bg img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__card-bg-mobile {
        display: none;

        @include vp-767 {
            display: block;
            position: absolute;
            bottom: 0;
            right: 0;
            width: 200px;
            height: 123px;
        }
    }

    &__card-bg-mobile img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__subtitle {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        margin: 0 0 34px;

        @include vp-1279 {
            font-size: 40px;
            margin-bottom: 20px;
        }
        
        @include vp-767 {
            font-size: 35px;
        }

        @media screen and (max-width: 520px) {
            font-size: 30px;
        }
    }

    .intro-lk__card--one .intro-lk__subtitle {
        color: rgba(60, 43, 139, 1);
    }

    .intro-lk__card--two .intro-lk__subtitle {
        color: rgba(255, 255, 255, 1);
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        align-items: start;
        flex-grow: 1;
        position: relative;
        z-index: 1;

        @media screen and (max-width: 520px) {
            flex-direction: column;
            gap: 20px;
        }
    }

    &__name {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 36px;
        leading-trim: NONE;
        line-height: 120%;
        letter-spacing: 0.5px;
        text-align: center;
        vertical-align: middle;
        display: block;
        background: rgba(116, 200, 8, 1);
        color: rgba(255, 255, 255, 1);
        padding: 8px 30px;
        border-radius: 34px;

        @include vp-1023 {
            font-size: 28px;
            line-height: 100%;
        }

        @include vp-767 {
            font-size: 26px;
        }

        @media screen and (max-width: 520px) {
            font-size: 22px;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: 10px;
        height: 100%;
        align-items: end;

        @media screen and (max-width: 520px) {
            align-items: flex-start;
        }
        
    }

    &__link {
        font-family: Inter;
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 24.8px;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: rgba(81, 60, 176, 1);
        transition: .3s;

        @include vp-1023 {
            font-size: 16px;
        }

        @include vp-389 {
            font-size: 14px;
        }
    }

    &__link:hover {
        opacity: 0.8;
    }


    &__btn {
        max-width: 100%;
        background: rgba(81, 60, 176, 1);
        padding: 4px 12px;
        border-radius: 30px;
        font-family: Inter;
        font-weight: 700;
        font-style: Bold;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 24.8px;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: rgba(255, 255, 255);
        margin-top: auto;
        transition: .3s;

        @media screen and (max-width: 520px) {
            margin-top: 10px;
            padding: 8px 14px;
        }
    }

    &__btn:hover {
        opacity: 0.8;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }


    &__item {
        display: flex;
        justify-content: space-between;
        align-items: end;
        gap: 15px;

        p,
        span {
            font-family: Inter;
            font-weight: 700;
            font-style: Bold;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 24.8px;
            letter-spacing: 0%;
            
            vertical-align: middle;
            color: rgba(255, 255, 255, 1);

            @include vp-1023 {
                font-size: 16px;
                line-height: 120%;
            }

            @include vp-389 {
                font-size: 14px;
            }
        }

        p {
            margin: 0;
        }

        @media screen and (max-width: 520px) {
            gap: 20px;
        }
    }

    &__item:last-child {
        @include vp-767 {
            display: block;
        }
    }

    &__last {
        margin-top: 10px !important;
        text-align: center;

        @include vp-767 {
            background: rgba(255, 255, 255, 1);
            border-radius: 10px;
            padding: 10px;
            color: rgba(81, 60, 176, 1) !important;
        }
    }
}