.regist {
    overflow: hidden;
    margin-top: -100px;
    position: relative;
    z-index: 1;
    &__wrapper {
        padding: 90px 70px;
        border-radius: 60px 60px 0 0;
        background: rgba(255, 255, 255, 1);

        @media screen and (max-width: 1470px) {
            padding: 90px 40px;
        }

        @include vp-1023 {
            border-radius: 32px 32px 0 0;
            padding: 90px 20px;
        }

        @media screen and (max-width: 660px) {
            padding: 60px 20px;
        }

        @include vp-389 {
            padding: 50px 10px;
        }
    }



    &__title {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        color: rgba(81, 60, 176, 1);
        margin: 0 0 30px;

        @include vp-1279 {
            font-size: 50px;
        }

        @include vp-767 {
            font-size: 45px;
        }

        @media screen and (max-width: 520px) {
            font-size: 35px;
        }

        @include vp-389 {
            font-size: 30px;
        }
    }

    &__table {
        display: flex;
        flex-direction: column;
        gap: 26px;

        @include vp-767 {
            gap: 20px;
        }
    }

    &__top {
        display: grid;
        grid-template-columns: 1.2fr 1fr 1fr 1fr;
        gap: 10px;
        font-family: Inter;
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 24.8px;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(60, 43, 139, 1);
        border-bottom: 1px solid rgba(60, 43, 139, 1);
        padding-bottom: 20px;
        div:nth-child(2),
        div:nth-child(3) {
            justify-self: center;
        }

        @include vp-1279 {
            grid-template-columns: 0.5fr 1fr 1fr 1fr;
        }

        @include vp-767 {
            font-size: 16px;
            padding-bottom: 10px;
        }

        div:last-child {
            @media screen and (max-width: 660px) {
                display: none;
            }
        }

        @media screen and (max-width: 660px) {
            grid-template-columns: 0.5fr 0.7fr 1fr;

            div:nth-child(3) {
                justify-self: end;
            }

            div:nth-child(2) {
                justify-self: start;
            }
        }

        @include vp-389 {
            grid-template-columns: 0.3fr 0.5fr 1fr;
        }
        
    }

    &__top div:last-child {
        justify-self: end;
    }

    &__row {
        display: grid;
        grid-template-columns: 1.2fr 1fr 1fr 1fr;
        gap: 10px;

        div {
            font-family: Inter;
            font-weight: 400;
            font-style: Regular;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 100%;
            letter-spacing: 0%;
            color: rgba(60, 43, 139, 1);

            @include vp-767 {
                font-size: 16px;
            }
        }

        div:nth-child(2),
        div:nth-child(3) {
            justify-self: center;
        }

        .regist__green {
            font-weight: 700;
            color: rgba(116, 200, 8, 1);
            justify-self: end;

            @media screen and (max-width: 660px) {
                display: none;
            }
        }

        @include vp-1279 {
            grid-template-columns: 0.5fr 1fr 1fr 1fr;
        }

        
        @media screen and (max-width: 660px) {
            grid-template-columns: 0.5fr 0.7fr 1fr;

            div:nth-child(3) {
                justify-self: end;
            }

            div:nth-child(2) {
                justify-self: start;
            }
        }

        @include vp-389 {
            grid-template-columns: 0.3fr 0.5fr 1fr;
        }
    }

    &__row-text {
        display: none;

        @media screen and (max-width: 660px) {
            display: block;
            font-family: Inter;
            font-weight: 700;
            font-style: Bold;
            font-size: 14px;
            leading-trim: NONE;
            line-height: 120%;
            letter-spacing: 0%;
            vertical-align: middle;
            color: rgba(116, 200, 8, 1);
            margin: 10px 0 0;
        }
    }
}